@import "helper";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  background-color: #f5f5f5;
  color: #333;
}
ul,ol {
  list-style: none;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
button {
  font: inherit;
}
:focus {
  outline: none;
}
.icon {
  width: 1em;
  height: 1em;
}
.clearfix {
  content: "";
  display: block;
  clear: both;
}
.oneLine {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}